import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HTTP_INTERCEPTORS } from '@angular/common/http';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NavigationModule } from './navigation/navigation.module';
import { AddInterestCohortInterceptor } from './add-interest-cohort.interceptor';

@NgModule({
  declarations: [AppComponent],
  imports: [BrowserModule, AppRoutingModule, NavigationModule],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: AddInterestCohortInterceptor, multi: true }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
